import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function Category() {
  const navigate = useNavigate();

  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const limit = 5;

  const [selectedCategory, setSelectedCategory] = useState(null);
  const [subcategories, setSubcategories] = useState([]);
  const [subLoading, setSubLoading] = useState(false);

  const [selectedSubcategory, setSelectedSubcategory] = useState(null);
  const [products, setProducts] = useState([]);
  const [productsLoading, setProductsLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    axios
      .get(`http://localhost:3000/api/categories?page=${page}&limit=${limit}`)
      .then(res => {
        setCategories(Array.isArray(res.data.categories) ? res.data.categories : []);
        setTotalPages(res.data.totalPages || 1);
        setLoading(false);
      })
      .catch(err => {
        setError('Failed to load categories');
        setLoading(false);
      });
  }, [page]);

  const getImageSrc = (cat) => {
    if (!cat?.imageUrl) return '/images/default-category.jpg';
    if (cat.imageUrl.startsWith('http')) return cat.imageUrl;
    return `http://localhost:3000${cat.imageUrl}`;
  };

  const handleCategoryClick = async (cat) => {
    if (!cat || !cat.id) return;
    setSelectedCategory(cat);
    setSelectedSubcategory(null);
    setProducts([]);
    setSubcategories([]);
    setSubLoading(true);
    setProductsLoading(false);

    try {
      const subRes = await axios.get(`http://localhost:3000/api/subcategories?categoryId=${cat.id}`);
      let subs = Array.isArray(subRes.data)
        ? subRes.data
        : Array.isArray(subRes.data.subcategories)
          ? subRes.data.subcategories
          : [];
      setSubcategories(subs);
    } catch (e) {
      setSubcategories([]);
    }
    setSubLoading(false);
  };

  const handleSubcategoryClick = async (subcat) => {
    if (!subcat || !subcat.id) {
      console.warn('Invalid subcategory:', subcat);
      return;
    }
    setSelectedSubcategory(subcat);
    setProductsLoading(true);
    setProducts([]);
    try {
      const prodRes = await axios.get(`http://localhost:3000/api/products?subcategoryId=${subcat.id}`);
      let prods = Array.isArray(prodRes.data)
        ? prodRes.data
        : Array.isArray(prodRes.data.products)
          ? prodRes.data.products
          : [];
      setProducts(prods);
    } catch (e) {
      setProducts([]);
    }
    setProductsLoading(false);
  };

  // Add to Cart handler
  const handleAddToCart = async (productId) => {
    try {
      // Adjust API and payload as per your backend
      await axios.post('http://localhost:3000/api/cart', {
        productId,
        quantity: 1
      });
      alert('Product added to cart!');
    } catch (err) {
      alert('Failed to add to cart!');
    }
  };

  const colClass = "col-12 col-sm-6 col-md-4 col-lg-3 mb-4";

  if (loading) return <div className="text-center my-5">Loading...</div>;
  if (error) return <div className="text-danger">{error}</div>;

  return (
    <section className="py-5 overflow-hidden">
      <div className="container-lg">
        {/* Category Grid */}
        <div className="row justify-content-center">
          <div className="col-md-12">
            <div className="d-flex flex-wrap justify-content-center" style={{ gap: '24px' }}>
              {categories.map(cat => (
                <button
                  type="button"
                  className="text-center category-card"
                  key={cat.id}
                  style={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    padding: '18px',
                    margin: '8px',
                    borderRadius: '16px',
                    background: selectedCategory && selectedCategory.id === cat.id ? '#e3f2fd' : '#fff',
                    boxShadow: '0 2px 8px rgba(0,0,0,0.08)',
                    minWidth: 140,
                    maxWidth: 180,
                    transition: 'box-shadow 0.2s, background 0.2s',
                    textDecoration: 'none',
                    color: 'inherit',
                    border: selectedCategory && selectedCategory.id === cat.id ? '2px solid #1976d2' : 'none',
                    cursor: 'pointer'
                  }}
                  onClick={() => handleCategoryClick(cat)}
                  onMouseOver={e => e.currentTarget.style.boxShadow = '0 4px 16px rgba(25,118,210,0.12)'}
                  onMouseOut={e => e.currentTarget.style.boxShadow = '0 2px 8px rgba(0,0,0,0.08)'}
                >
                  <img
                    src={getImageSrc(cat)}
                    className="rounded-circle"
                    alt={cat.name + ' Thumbnail'}
                    style={{
                      width: 100,
                      height: 100,
                      objectFit: 'cover',
                      marginBottom: 12,
                      border: '3px solid #eee',
                      background: '#fafafa'
                    }}
                    onError={e => { e.target.onerror = null; e.target.src = '/images/default-category.jpg'; }}
                  />
                  <h4 className="fs-6 fw-semibold category-title" style={{ marginTop: 8 }}>{cat.name}</h4>
                </button>
              ))}
            </div>
            {/* Pagination Controls */}
            <div className="pagination mt-4 d-flex justify-content-center align-items-center">
              <button
                className="btn btn-outline-primary"
                onClick={() => setPage(page - 1)}
                disabled={page === 1}
              >
                Prev
              </button>
              <span className="mx-3">Page {page} of {totalPages}</span>
              <button
                className="btn btn-outline-primary"
                onClick={() => setPage(page + 1)}
                disabled={page === totalPages}
              >
                Next
              </button>
            </div>
          </div>
        </div>

        {/* Subcategory Buttons */}
        {selectedCategory && (
          <div className="row mt-4">
            <div className="col-12 d-flex flex-wrap justify-content-center" style={{ gap: '12px' }}>
              {subLoading ? (
                <div>Loading subcategories...</div>
              ) : subcategories.length === 0 ? (
                <div className="text-secondary">No subcategories found for "{selectedCategory.name}".</div>
              ) : (
                subcategories.map(subcat => (
                  <button
                    key={subcat?.id}
                    className={`btn ${selectedSubcategory && selectedSubcategory.id === subcat.id ? 'btn-primary' : 'btn-outline-primary'}`}
                    onClick={() => handleSubcategoryClick(subcat)}
                    disabled={!subcat || !subcat.id}
                  >
                    {subcat?.name || "Unnamed"}
                  </button>
                ))
              )}
            </div>
          </div>
        )}

        {/* Products Grid */}
        {selectedSubcategory && (
          <div className="row mt-4">
            <div className="col-12">
              <h5 className="mb-3 text-primary">
                {selectedSubcategory.name} Products
              </h5>
              {productsLoading ? (
                <div>Loading products...</div>
              ) : (Array.isArray(products) && products.length === 0) ? (
                <div className="text-secondary">No products found.</div>
              ) : (
                <div className="row">
                  {(Array.isArray(products) ? products : []).map(product => (
                    <div className={colClass} key={product.id}>
                      <div className="card h-100 shadow-sm">
                        <img
                          src={product.imageUrl || '/images/default-product.jpg'}
                          className="card-img-top"
                          alt={product.name}
                          style={{ objectFit: 'cover', height: 180 }}
                          onError={e => { e.target.onerror = null; e.target.src = '/images/default-product.jpg'; }}
                        />
                        <div className="card-body d-flex flex-column">
                          {/* Product Name as link */}
                          <h6
                            className="card-title text-primary"
                            style={{ cursor: 'pointer' }}
                            onClick={() => navigate(`/products/${product.id}`)}
                          >
                            {product.name}
                          </h6>
                          <p className="card-text mb-1">{product.price}</p>
                          <div className="mt-auto d-flex gap-2">
                            {/* View Details Button */}
                            <button
                              className="btn btn-sm btn-outline-info"
                              onClick={() => navigate(`/products/${product.id}`)}
                            >
                              View Details
                            </button>
                            {/* Add to Cart Button */}
                            <button
                              className="btn btn-sm btn-success"
                              onClick={() => handleAddToCart(product.id)}
                            >
                              Add to Cart
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </section>
  );
}

export default Category;
